@import "../../mixins/index";

.antTreeSwitcherIcon() {
  position: relative;
  &:after {
    .iconfont-size-under-12px(6px);
    display: inline-block;
    .iconfont-font("\e606");
    font-weight: bold;
    position: absolute;
    top: 0;
    right: 4px;
    color: #666;
    transition: transform .3s ease;
  }
}



